<template>
  <div class="lang-switch">
    <ul>
      <li @click="changeLang('en')">Aa</li>
      <li @click="changeLang('cn')">中</li>
      <li @click="changeLang('ja')">日</li>
    </ul>
  </div>
</template>

<script>
export default {
  methods: {
    changeLang(lang) {
      localStorage.site_lang = lang;
      this.$i18n.locale = lang;
    },
  },
};
</script>

